import { configureStore } from "@reduxjs/toolkit";
import { currentScreenSlice } from "./slices";
import { langSlice } from "./slices";
import { fetchPostsSlice } from "./slices";
import { favoritePostsSlice } from "./slices";
import { createListenerMiddleware } from "@reduxjs/toolkit";
import { openPostSlice } from "./slices";
import { addPostActiveSlice } from "./slices";



const addFavoritPostListenerMiddleware = createListenerMiddleware({
    extra: undefined,
    onError: (e) => {
        console.log('[addFavoritPostListenerMiddleware]: ', e)
    }
})


addFavoritPostListenerMiddleware.startListening({
    type: 'favoritePosts/addFavoritPost',
    effect: (action, api) => {
        console.log('Post added')

        let favoritePosts = JSON.parse(localStorage.getItem('favoritePosts'))
        favoritePosts = [...favoritePosts, action.payload]
        localStorage.setItem('favoritePosts', JSON.stringify(favoritePosts)) 

        console.log(JSON.parse(localStorage.getItem('favoritePosts')))
    }
})


const removeFavoritPostListenerMiddleware = createListenerMiddleware({
    extra: undefined,
    onError: (e) => {
        console.log('[removeFavoritPostListenerMiddleware]: ', e)
    }
})


removeFavoritPostListenerMiddleware.startListening({
    type: 'favoritePosts/removeFavoritPost',
    effect: (action, api) => {
        console.log('Post removed')

        let favoritePosts = JSON.parse(localStorage.getItem('favoritePosts')) 
        let favoritePostsWithoutRemoved = favoritePosts.filter((post, index) => post.uuid !== action.payload.uuid)
        localStorage.setItem('favoritePosts', JSON.stringify(favoritePostsWithoutRemoved))

        console.log(JSON.parse(localStorage.getItem('favoritePosts')))
    }
})

 export const store = configureStore({
    reducer: {
        currentScreen: currentScreenSlice.reducer,
        lang: langSlice.reducer,
        fetch: fetchPostsSlice.reducer,
        favoritePosts: favoritePostsSlice.reducer,
        openPost: openPostSlice.reducer,
        addPostActive: addPostActiveSlice.reducer,
    },
    middleware: (getDefaultMiddleware) => getDefaultMiddleware().prepend([addFavoritPostListenerMiddleware.middleware,
         removeFavoritPostListenerMiddleware.middleware])
})


