import { useDispatch, useSelector } from 'react-redux'
import { Suspense, lazy, useRef } from 'react'
import { useEffect } from 'react'
import Panel from './components/Panel.js'
import { Loading } from './components/Loading.js'
import { fetchPosts } from './store/slices.js'
import Like from './assets/Like.jsx'
import { useState } from 'react'
import { texts } from './assets/texts.js'
import { addFavoritPost } from './store/slices.js'
import { removeFavoritPost } from './store/slices.js'
import { memo } from 'react'
import { openPost } from './store/slices.js'
import urls from './assets/urls.js'
import TopPanel from './components/TopPanel.js'


const AddPost = lazy(() => import('./components/AddPost.js'))
const Favorites = lazy(() => import('./components/Favorites.js'))
const InPost = lazy(() => import('./components/InPost.js'))



function MainScreen() {

  const fetchState = useSelector(state => state.fetch)
  const dispatch = useDispatch()


  useEffect(() => {
    dispatch(fetchPosts())

    if (!localStorage.getItem('favoritePosts')) {
      localStorage.setItem('favoritePosts', JSON.stringify([]))
    }

  }, [])


  function Post({ name, author, description, ingredients, method, uuid}) {

    const postImage = useRef(null)

    const currentLang = useSelector(state => state.lang)


    const [color, setColor] = useState('none')


    function random(min, max) {
      min = Math.ceil(min)
      max = Math.floor(max)
      return Math.floor(Math.random() * (max - min + 1)) + min
    }

    if (!localStorage.getItem('postImageUrl' + uuid)) {
      localStorage.setItem('postImageUrl' + uuid, urls[random(0, 9)])
    }
    const postImageUrl = localStorage.getItem('postImageUrl' + uuid)


    useEffect(() => {
      postImage.current.style.backgroundImage = `url(${postImageUrl})`

      if (JSON.parse(localStorage.getItem('favoritePosts')).length > 0) {
        for (let i = 0; i < JSON.parse(localStorage.getItem('favoritePosts')).length; i++) {
         
          if (JSON.parse(localStorage.getItem('favoritePosts'))[i].uuid == uuid) {
            setColor('red')
          }
      }
      }
    }, [])

    function postLikeHandler() {

      const currentPost = {
        uuid: uuid,
        postImageUrl: postImageUrl,
        name: name,
        description: description,
        author: author,
        ingredients: ingredients,
        method: method,
      }

      if (color == 'none') {
        dispatch(addFavoritPost(currentPost))
        setColor('red')
      } else {
        dispatch(removeFavoritPost(currentPost))
        setColor('none')
      }

    }

    function openHandler() {
        dispatch(openPost({
            name: name,
            description: description,
            ingredients: ingredients,
            method: method,
            postImageUrl: postImageUrl,
        }))
    }

    return (
      <div className='post'>
        <div className='post__image' ref={postImage}>
          <div className='post__author'>{author}</div>
          <div onClick={postLikeHandler} className='post__like'>
            <Like fill={color} />
          </div>
        </div>
        <div className='post__content'>
          <div className='post__name'>{name}</div>
          <div className='post__desc'>{description}</div>
          <div className='post__open-wrapper' onClick={openHandler}>
            <div className='post__open'>{texts[currentLang].open}</div>
          </div>
        </div>
      </div>
    )
  }


  let posts

  if (typeof fetchState === 'object') {
    posts = fetchState.map(i => {
      return (<Post name={i.Name} author={i.Author} description={i.Description} ingredients={i.Ingredients} method={i.Method} uuid={i.order} />)
    })
  }

  return (
    
    <Suspense fallback={(<Loading />)}>
      <InPost />
      <AddPost />
      <div className="main-screen">
        {
          typeof fetchState === 'object' ? (<div>{

            posts

          }</div>) : (<Loading />)
        }
      </div>
    </Suspense>

  )
}



export function App() {

  const currentScreen = useSelector(state => state.currentScreen)

  return (
    <div>
      
      <Suspense fallback={(<Loading />)}>
        {
          currentScreen === 'MainScreen' ? (<MainScreen />) :
          currentScreen === 'Favorites' ? (<Favorites />) :
                (<Loading />)
        }
      </Suspense>
      <TopPanel />
      <Panel />
      
    </div>
  )
}