import { createSlice } from "@reduxjs/toolkit";
import { createAsyncThunk } from "@reduxjs/toolkit";


function random(min, max) {
    min = Math.ceil(min)
    max = Math.floor(max)
    return Math.floor(Math.random() * (max - min + 1)) + min
}

function uniqueRandom(min, max, iterations) {
    let numbers = []
    let i = 0
    while (i < iterations) {
        let number = random(min, max)
        if (numbers.includes(number)) {
            console.log('includes')
        } else {
            numbers.push(number)
            i = i + 1
        }
    }

    return numbers
     
}


export const fetchPosts = createAsyncThunk(
    'fetch/fetchUsers',
    async (arg, thunckApi) => {
        let data = await import('../assets/recipes.json')
        return data

    }
)


export const fetchPostsSlice = createSlice({
    name: 'fetch',
    initialState: 'no users',
    extraReducers: (builder) => {
        builder.addCase(fetchPosts.pending, (state, action) => {

            state = 'pending'
            return state
        })

        builder.addCase(fetchPosts.fulfilled, (state, action) => {
            state = []
            const posts = uniqueRandom(0, 1000, 100)

            for (let i = 0; i < posts.length; i++) {

                let objectOfPost = { ...action.payload[posts[i]], order: Array.from(action.payload).indexOf(action.payload[posts[i]]) }
                state = [...state, objectOfPost]
            }
            return state
        })

        builder.addCase(fetchPosts.rejected, (state, action) => {
            state = 'rejected'
            return state
        })
    }
})

export const currentScreenSlice = createSlice({
    name: 'currentScreen',
    initialState: 'MainScreen',
    reducers: {
        switchScreen: (state, action) => {
            state = action.payload
            return state
        }
    }
})

export const { switchScreen } = currentScreenSlice.actions


export const langSlice = createSlice({
    name: 'lang',
    initialState: 'ru_RU',
    reducers: {
        switchLang: (state, action) => {
            state = action.payload
            return state
        }
    }
})

export const { switchLang } = langSlice.actions



let favoritePostsSliceInitialState = []

export const favoritePostsSlice = createSlice({
    name: 'favoritePosts',
    initialState: favoritePostsSliceInitialState,
    reducers: {

        addFavoritPost: (state = favoritePostsSliceInitialState, action) => {
            state = [...state, { add: action.payload }]
            return state
        },

        removeFavoritPost: (state = favoritePostsSliceInitialState, action) => {
            state = [...state, { remove: action.payload }]
            return state
        }
    }

})

export const { addFavoritPost, removeFavoritPost } = favoritePostsSlice.actions



