import Home04Icon from "../assets/MainScreen"
import LayerAddIcon from "../assets/AddPost"
import Bookmark02Icon from "../assets/Favorites"
import { UseDispatch, useDispatch } from "react-redux"
import { switchScreen } from "../store/slices"
import { switchLang } from "../store/slices"
import { changeAddPostState } from "../store/slices"
import { useSelector } from "react-redux"
import { useEffect } from "react"



export default function Panel() {

    const dispatch = useDispatch()

    function setActive(sectionLineNumber) {
        let sectionLines = document.querySelectorAll('.panel__section-line')

        for (let i = 0; i <= sectionLines.length; i++) {
            try {
                sectionLines[i].classList.remove('panel__section-line--active')
            } catch { }
        }
        sectionLines[sectionLineNumber].classList.add('panel__section-line--active')
    }

    return (
        <div className="panel">
            <div className="panel__inner">


                <div onClick={() => {
                    dispatch(switchScreen('MainScreen'))
                    setActive(0)
                }} className="panel__section">
                    <Home04Icon />
                    <div className="panel__section-line panel__section-line--active"></div>
                </div>
                <div onClick={() => {
                    dispatch(changeAddPostState(true))
                    setActive(1)
                }} className="panel__section">
                    <LayerAddIcon />
                    <div className="panel__section-line"></div>
                </div>
                <div onClick={() => {
                    dispatch(switchScreen('Favorites'))
                    setActive(2)
                }} className="panel__section">
                    <Bookmark02Icon />
                    <div className="panel__section-line"></div>
                </div>
            </div>
        </div>
    )
}