import { useSelector } from "react-redux"
import { texts } from "../assets/texts"
import { useEffect, useRef } from "react"
import { languages } from "../assets/texts"
import { useState } from "react"
import { switchLang } from "../store/slices"
import { useDispatch } from "react-redux"
import Tick from "../assets/Tick"

export default function TopPanel() {

    const currentLang = useSelector(state => state.lang)
    const [selectActive, setSelectActive] = useState(false)
    const select = useRef(null)

    const dispatch = useDispatch()


    function Select() {

        let selectValues = languages.map(lang => {
            return (<div className="top-panel__select-value" onClick={() => dispatch(switchLang(lang[0]))}>
                <div className="top-panel__value-text">{lang[1]}</div>
                {
                    lang[0] == currentLang ? (<Tick />) : (<div></div>)
                }
            </div>)
        })

        useEffect(() => {
        
            if (selectActive) {
                select.current.classList.add('top-panel__select--active')
            } else {
                select.current.classList.remove('top-panel__select--active')
            }
    
        }, [selectActive])
    
        return (
            <div ref={select} className="top-panel__select">        
                {
                    selectValues
                }        
            </div>
        )
    }

    useEffect(() => {

        function handleClick() {
            if (selectActive) {
                select.current.classList.remove('top-panel__select--active') 
                setSelectActive(false)
            }
        }

        document.addEventListener('click', handleClick)

        return () => document.removeEventListener('click', handleClick)
    })
   
    return (
        <div className="top-panel">
            <div className="top-panel__inner">
                <div className="top-panel__current-lang">{texts[currentLang].currentLanguage}</div>
                <div className="top-panel__select-button" onClick={(e) => {
                e.stopPropagation() 
                setSelectActive(!selectActive)}}>
                    <div className="top-panel__select-point"></div>
                    <div className="top-panel__select-point"></div>
                    <div className="top-panel__select-point"></div>
                    <Select />
                </div>
            </div>
            
        </div>
    )
}